var throttle_fn_key = {};
var temp = '';
// 
const throttle = (method, context, time = 100) => {
	temp = method.name || method.toString();
	throttle_fn_key[temp] && clearTimeout(throttle_fn_key[temp]);  // mthodcontextwindow
	throttle_fn_key[temp] = setTimeout(function () {
		method.call(context)   // 
	}, time);
}

//
const debounce = (method, delay = 50, duration = 100) => {
	temp = method.name || method.toString();
	var timer = null, stime = new Date();          // 
	return function () {
		var context = this, args = arguments,
			ctime = new Date();   // 

		timer && clearTimeout(timer);     // 

		// duration
		if (ctime - stime >= duration) {
			method.apply(context, args);
			stime = ctime;
		} else {
			timer = setTimeout(function () {
				method.apply(context, args);
			}, delay);
		}
	}
}

export default {
	throttle,
	debounce
};



